.navbar {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
  padding-top: 50px;
}

.brand {
  font-size: 2em;
  font-weight: bold;
}

.navbar-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.nav-item {
  text-decoration: none;
  color: #dadada;
  margin: 10px 20px;
  font-weight: 500;
  font-size: 1.2em;
  display: inline-block;
  position: relative;
  padding: 20px 0px;
  margin: 0px 20px;
}

.nav-link:hover {
  opacity: 1;
}

.nav-link::before {
  transition: 300ms;
  height: 2px;
  left: 0;
  content: "";
  position: absolute;
  background-color: #d95a44;
}

.nav-link-ltr::before {
  width: 0%;
  bottom: 10px;
}

.nav-link-ltr:hover::before {
  width: 100%;
}

.divider {
  width: 1px;
  height: 35%;
  background-color: #b6b6b6b2;
  margin-right: 10px;
}

.social-i {
  position: relative;
  cursor: pointer;
  padding-top: 5px;
}

.social-i::before {
  transition: 300ms;
  height: 2px;
  content: "";
  position: absolute;
  background-color: #d95a44;
  width: 0%;
  bottom: 0;
}

.social-i:hover::before {
  width: 100%;
}

.navbar2 {
  display: none;
  justify-content: space-between;
  padding: 20px;
  align-items: center;
}

.navbar-links2 {
  top: 80px;
  display: none;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  width: 100%;
  z-index: 10;
  position: absolute;
  background-color: #2b2b34;
}

.add-height {
  height: 100% !important;
}

.toggleNav {
  display: flex;
}

@keyframes toggleNav {
  from {
    right: -100%;
  }
  to {
    right: 0;
  }
}

@media only screen and (max-width: 600px) {
  .navbar {
    display: none;
  }
  .navbar2 {
    display: flex;
  }
}
