.gallery-view {
  display: grid;
  grid-template-columns: 300px 300px 300px;
  column-gap: 20px;
  row-gap: 20px;
  width: 60%;
  margin: 0 auto;
}

.gallery-card-wrapper {
  position: relative;
  width: 300px;
}

.overlay {
  position: absolute;
  background-color: #3d4044;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  left: 20px;
  height: 80%;
  top: 20px;
  opacity: 0;
  transition: all 0.5s;
  font-size: 1em;
}

.overlay:hover {
  opacity: 1;
}

.overlay span {
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .gallery-view {
    grid-template-columns: 300px;
    width: 100%;
    justify-content: center;
  }
}
