.App {
  text-align: center;
  color: #fff;
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
}

.myname {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name-head {
  font-weight: bold;
}

.myname h1 {
  font-size: 3.5em;
}

.myname h3 {
  font-size: 1em;
  /* color: #d95a44; */
}

.about-dscrip {
  margin-top: 10px;
  max-width:600px;
  text-align: left;
  color: rgba(255, 255, 255, 0.774);
  font-weight: 400;
}

.underline {
  height: 2px;
  width: 50px;
  background-color: #d95a44;
  margin-top: 20px;
  margin-bottom: 10px;
}

.btn {
  margin-top: 20px;
  border: 2px solid #d95a44;
  background-color: transparent;
  color: #d95a44;
  padding: 10px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  background-color: #d95a44;
  color: #fff;
}

.profile-img {
  width: 400px;
  overflow: hidden;
  background-color: #2b2b34;
  border-radius: 70% 30% 30% 70% / 60% 40% 60% 40%;
}

.img-bg {
  left: 4px;
  top: 0;
  position: absolute;
  width: 400px;
  height: 450px;
  background-color: #424250;
  z-index: -4;
  transform: rotate(49deg);
  border-radius: 70% 30% 30% 70% / 60% 40% 60% 40%;
}

.img-container {
  position: relative;
}

.social-links {
  display: none;
}

@media only screen and (max-width: 600px) {
  .home {
    /* flex-direction: column; */
    padding: 0 10px;
  }
  .myname {
    padding-top: 100px;
    margin-right: auto;
  }
  .myname h1 {
    font-size: 2.5em;
  }
  .about-dscrip {
    max-width: 400px;
  }
  .profile-img img {
    height: 233px;
    width: 120px;
  }
  .social-links {
    margin-top: 10px;
    display: flex;
  }

  .profile-img {
    width: 150px;
  }

  .img-bg {
    height: 150px;
    width: 150px;
    top: 8px;
    left: 11px;
    transform: rotate(154deg);
  }
}
