.portfolio {
  height: 100vh;
  /* background-color: #34343b; */
}
.head {
  padding-top: 100px;
}
.head span {
  color: #d95a44;
  font-weight: bold;
  letter-spacing: 1px;
}

.head h2 {
  font-size: 2em;
  color: #dadada;
}

.gallery {
  margin-top: 50px;
}

.filterMenu {
  width: 38%;
  margin: 20px auto;
  padding: 20px 0;
}

.filterMenu a {
  padding: 20px;
  font-weight: 500;
  text-transform: uppercase;
  color: #dadadac5;
  transition: all 0.2s;
  cursor: pointer;
}

.filterMenu a:hover {
  color: #d95a44;
}

.port-line {
  margin-top: 20px;
  width: 100%;
  height: 2px;
  background-color: #dadada;
}

.fil-active {
  color: #d95a44 !important;
  border-bottom: 2px solid #d95a44;
}

@media only screen and (max-width: 600px) {
  .filterMenu {
    width: 100%;
  }

  .portfolio {
    height: auto;
  }

  .filterMenu a {
    padding: 10px;
    font-size: 10px;
  }
}
