.contact {
  margin-top: 50px;
  height: 90vh;
}

.social-links-container {
  display: grid;
  grid-template-columns: 400px 400px;
  margin: 50px auto;
  column-gap: 50px;
  row-gap: 50px;
  width: 70%;
  justify-content: center;
  align-content: center;
}

.social-item {
  background-color: #36363e;
  display: flex;
  padding: 20px;
  align-items: center;
}

.social-item a {
  margin-left: 20px;
  color: #d95a44;
  text-decoration: none;
  font-size: 22px;
}

.scrolltop {
  position: absolute;
  height: 50px;
  width: 50px;
  border: 2px solid #d95a44;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  right: 15%;
}

.scrolltop:hover {
  background-color: #d95a44;
}

.footer {
  margin-top: 20px;
  border-top: 2px solid #d95a44;
  padding: 30px;
}

@media only screen and (max-width: 600px) {
  .contact {
    height: auto;
    position: relative;
    padding-bottom: 50px;
  }

  .scrolltop {
    bottom: 0;
  }
  .social-links-container {
    grid-template-columns: auto;
  }

  .social-item {
    padding: 10px;
  }
  .social-item a {
    margin-left: 0;
  }
}
